import shoplist from '../../components/shoplist/shoplist.vue';
import loadGif from '@/components/loadGif/loadGif.vue';
import nav from '@/components/nav/nav.vue';
import { mapState } from 'vuex';
export default {
    data() {
        return {
            search: '', // 输入框插询的字段
            searchShopList: [],
            page: 1,  // 搜索用的page
            listSearchWords: [],
            searchCircle: '',  // 搜索参数
            circle: [], // 搜索页面的区域列表
            searchShopType: 0, // 搜索参数
            typeList: [], // 搜索行业的列表
            searchIntelligentSorting: 0, // 搜索参数
            intelligentSorting: [], // 智能排序列表
            serachFilter: 0, // 搜索参数
            filter: [], // 筛选的列表
            moduleValue: 0, // 模型值类型  默认为0
            subList: [], // 二级选择框使用的列表 如：距离  商圈等等
            searchContent: 41, // 输入的内容值
            queryName: '', // 在选择区县后  再输入的二级内容 地区名称
            show1: true, // 判断用显示一级下拉框
            show2: true, // 判断用显示二级下拉框
            oneList: [], // 一级下拉框用
            twoList: [], // 二级下拉框使用
            searchType: '', // 判断用
            circleType: '', // 判断二级列表时使用
            busy: true, // 加载判断用
            currentPage: 0, // 当前页
            nextPage: 0,
            maskShow: false,  // 判断 遮罩层 用
            searchValue: '', // 判断用
            searchValue2: '', // 判断用
            iconShow1: false,
            iconShow2: false,
            iconShow3: false,
            showLoadGif: true, // 控制加载动图的显示
            nShowNotHide: false // 判断下拉框出现
        };
    },
    computed: {
        ...mapState([
            'shopDetail', 'global', 'collectShopList', 'zoneId'
        ]),
        localCity () { // 计算出当前城市 用于监听数据
            return this.$store.state.localCity;
        },
        searchTypeValue() {  // 当vuex中的数值变化时 跳转搜索
            return this.$store.state.searchTypeValue;
        }
    },
    watch: {
        // 监听search的变化
        search: function(newval, oldval) {
            this.$data.page = 1; // 发现search变化后 将page变为1
        },
        localCity: function(newval, oldval) {
            // 城市发生变化后  重新加载筛选条件
            let that = this;
            this.axios({
                method: 'post',
                url: this.$store.state.global.hftcomClient,
                data: '{"id":19,"jsonrpc":"2.0","method":"listSearchWords","params":{"city":"' + this.$store.state.localCity + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                that.$data.circle = result.data.result.circle;
                that.$data.typeList = result.data.result.type;
                that.$data.intelligentSorting = result.data.result.intelligentSorting;
                that.$data.filter = result.data.result.filter;
            });
        }
    },
    methods: {
        ListIsNull() {
            if (this.searchShopList.length < 1) {
                this.showLoadGif = false;
                this.$vux.toast.text('当前没有优惠券', 'bottom');
            }
        },
        iconBack() {
            this.iconShow1 = false;
            this.iconShow2 = false;
            this.iconShow3 = false;
        },
        hiddenMask() {
            this.nShowNotHide = false;
            this.show1 = true;
            this.maskShow = false;
            this.iconBack();
        },
        subListIsNull() { // 判断subList是否为空
            for (let i = 0; i < this.circle.list.length; i++) {
                const el = this.circle.list[i];
                el.subListIsNull = false; // 给每一个对象添加了一个新属性来判断sublist是否为空
                if (el.moduleValue === 3 | el.moduleValue === -1) {
                    if (el.subList.length < 1) {
                        el.subListIsNull = true;
                    }
                }
            }
        },
        showCircle() {
            this.$data.twoList = [];
            this.searchType = this.circle.zh;
            this.nShowNotHide = true;
            if (this.show1) {
                this.nShowNotHide = true;
                this.show1 = false;
                this.maskShow = true;
                this.iconShow1 = true;
                this.subListIsNull();// 判断是否有的sublist为空
                this.oneList = this.circle.list;
            } else {
                this.nShowNotHide = false;
                this.show1 = true;
                this.maskShow = false;
                this.iconBack();
            };
        },
        showType() {
            this.$data.twoList = [];
            this.searchType = this.typeList.zh; // 当前选择的参数为行业

            if (this.show1) {
                this.nShowNotHide = true;
                this.show1 = false;
                this.maskShow = true;
                this.iconShow2 = true;
            } else {
                this.nShowNotHide = false;
                this.show1 = true;
                this.maskShow = false;
                this.iconBack();
            };
            this.oneList = this.typeList.list;
        },
        showIntelligentSorting() {
            this.$data.twoList = [];
            this.searchType = this.intelligentSorting.zh;
            this.nShowNotHide = true;
            if (this.show1) {
                this.nShowNotHide = true;
                this.show1 = false;
                this.maskShow = true;
                this.iconShow3 = true;
            } else {
                this.nShowNotHide = false;
                this.show1 = true;
                this.maskShow = false;
                this.iconBack();
            };
            this.oneList = this.intelligentSorting.list;
        },
        showFilter() {
            this.$data.twoList = [];
            this.searchType = this.filter.zh;
            this.nShowNotHide = true;
            this.oneList = this.filter.list;
        },
        showValue1(one) {
            // 点击后将page设为1
            this.page = 1;
            // 点击一级下拉框事件
            this.searchValue = one.queryName; // 该参数只是用来判断
            if (this.searchType === this.typeList.zh) { // 行业
                // console.log(this.searchShopType);
                this.typeList.zh = one.queryName;
                this.searchShopType = one.value;
                this.nShowNotHide = false; // 收起下拉框
                this.show1 = true;
                this.maskShow = false;
                this.show();
            };
            if (this.searchType === this.intelligentSorting.zh) { // 智能排序
                this.searchIntelligentSorting = one.value;
                // console.log(this.searchIntelligentSorting);
                this.intelligentSorting.zh = one.queryName;
                this.searchIntelligentSorting = one.value;
                this.nShowNotHide = false; // 收起下拉框
                this.show1 = true;
                this.maskShow = false;
                this.show();
            };
            if (this.searchType === this.filter.zh) { // 筛选
                this.serachFilter = one.value;
            };
            // 如果为circle 可能会出现二级下拉框
            if (this.searchType === this.circle.zh) { // 商圈
                this.moduleValue = one.moduleValue;
                // 当modelValue 为-1 时 选择的为附近  进入附近二级下拉框
                if (this.moduleValue === -1) {
                    this.$data.twoList = this.$data.circle.list[1].subList;
                };
                // 当modelValue 为3 时 选择的为商圈  进入商圈二级下拉框
                if (this.moduleValue === 3) {
                    this.$data.twoList = this.$data.circle.list[2].subList;
                };
                if (this.moduleValue === -2) { // 其他  将集合置为空
                    this.$data.twoList = [];
                    this.queryName = one.queryName; // 设置queryname
                    this.circle.zh = this.queryName;
                    this.nShowNotHide = false; // 收起下拉框
                    this.show1 = true;
                    this.maskShow = false;
                    this.show();
                };
                if (this.moduleValue === 0) {  // 选择为全部时
                    this.queryName = ''; // 将选中其他区县的参数置为空
                    this.$data.twoList = [];
                    this.circle.zh = one.queryName;
                    this.nShowNotHide = false; // 收起下拉框
                    this.show1 = true;
                    this.maskShow = false;
                    this.show();
                };
            }
        },
        showValue2(two) {
            this.queryName = ''; // 将选中其他区县的参数置为空
            this.searchContent = two.value;
            this.circle.zh = two.name;
            this.searchValue2 = two.name;
            this.nShowNotHide = false; // 收起下拉框
            this.show1 = true;
            this.maskShow = false;
            this.show();
        },
        goBack () {
            this.$router.go(-1);
        },
        show () {
            let that = this;
            this.showLoadGif = true;
            console.log(this.$data.page);
            this.axios({
                method: 'post',
                url: this.global.hftcomClient,
                data: '{"id":19,"jsonrpc":"2.0","method":"searchShop","params":{"searchWord":"' + this.$data.search + '","type":' + this.$data.searchShopType + ',"userCode":"' + this.$store.state.userCode + '","longitude":' + this.$store.state.longitude + ',"latitude":' + this.$store.state.latitude + ',"page":' + this.$data.page + ',"city":"' + this.$store.state.localCity + '","moduleValue":' + this.$data.moduleValue + ',"content":' + this.$data.searchContent + ',"order":' + this.$data.searchIntelligentSorting + ',"filter":' + this.$data.serachFilter + ',"queryName":"' + this.$data.queryName + '","zoneId":"' + this.zoneId + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                if (that.page === 1) {
                    that.$data.searchShopList = result.data.result.shopList;
                } else {
                    that.$data.searchShopList = [...that.$data.searchShopList, ...result.data.result.shopList];
                };
                that.currentPage = result.data.result.page;
                that.nextPage = result.data.result.nextPage;
                that.$data.page++;
                that.busy = false;
                that.hiddenLoadGif();
                that.ListIsNull();
            });
            this.iconBack();
        },
        searchShopLoadMore () {
            this.busy = true;
            if (this.nextPage > this.currentPage) {
                this.show();
            } else {
                this.busy = true;
                this.hiddenLoadGif();
            }
        },
        hiddenLoadGif() {
            if (this.nextPage <= this.currentPage) {
                // this.$vux.toast.text('加载完毕', 'bottom');
                this.showLoadGif = false;
            }
        },
        isCollectShop: function() {
            let that = this;
            // 收藏的商户，用于收藏按钮的显示与否
            this.axios({
                method: 'post',
                url: this.$store.state.global.hftcomShop,
                data: '{"id":19,"jsonrpc":"2.0","method":"collectShop","params":{"userCode":"' + this.$store.state.userCode + '","latitude":' + this.$store.state.latitude + ',"longitude":' + this.$store.state.longitude + '}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                if (result.data.result === 'null') {
                    that.$store.state.collectShopList = [];
                } else {
                    that.$store.state.collectShopList = result.data.result[0];
                }
                console.table(JSON.parse(JSON.stringify(this.collectShopList, ['isCollect', 'type'])));
            });
        }
    },
    beforeCreate() {   // 创建之前
    },
    created() {
        let that = this;
        this.axios({
            method: 'post',
            url: this.$store.state.global.hftcomClient,
            data: '{"id":19,"jsonrpc":"2.0","method":"listSearchWords","params":{"city":"' + this.$store.state.localCity + '","zoneId":"' + this.zoneId + '"}}',
            contentType: 'application/json',
            dataType: 'json'
        }).then((result) => {
            that.$data.circle = result.data.result.circle;
            that.$data.typeList = result.data.result.type;
            that.$data.intelligentSorting = result.data.result.intelligentSorting;
            that.$data.filter = result.data.result.filter;
                // 以下代码是为了解决异步调用的问题
            that.typeList.zh = that.$store.state.searchTypeName;
            that.searchShopType = that.$store.state.searchTypeValue;
            if (that.searchShopType === '6') {
                that.searchShopType = 0;
            }
            that.show(); // 查询
            that.$store.state.searchShopList = [];

            if (this.$store.state.searchTypeValue === 0) {
            } if (this.$store.state.searchTypeValue === '6') {
                this.$store.state.searchTypeName = '行业';
                this.searchShopType = 0;
            } else {
                this.typeList.zh = this.$store.state.searchTypeName;
                this.searchShopType = this.$store.state.searchTypeValue;
            }
        });
    },
    mounted() {
        this.isCollectShop();
    },
    beforeDestroy() {
        this.$store.state.searchTypeName = '行业';
        this.$store.state.searchTypeValue = 0;  // 在页面销毁之前重置了选项
    },
    components: {
        shoplist,
        'v-nav': nav,
        loadGif
    }
};



